import React from 'react';
//import connect from '@vkontakte/vkui-connect';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel'
import Root from '@vkontakte/vkui/dist/components/Root/Root'
import View from '@vkontakte/vkui/dist/components/View/View'
import PanelSpinner from '@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner'
import '@vkontakte/vkui/dist/vkui.css';
import queryString from 'query-string';
import Header from './common/Header'
import connect from '@vkontakte/vkui-connect'
import SubscribeBlock from "./common/SubscribeBlock";
import StatisticBlock from "./common/StatisticBlock";
import Search from "@vkontakte/vkui/dist/components/Search/Search";
import FixedLayout from "@vkontakte/vkui/dist/components/FixedLayout/FixedLayout";


var sprintf = require('sprintf-js').sprintf;


const reqSubscribeList = 'https://api.sovetromantica.com/api/v1/notify/vkapp/%s/get';
const switchSubscribe = 'https://api.sovetromantica.com/api/v1/notify/vkapp/%s/switch/%s/%s';
const reqStatisticUrl = 'https://api.sovetromantica.com/api/v1/notify/vkapp/statistic/%s';

class App extends React.Component {
    constructor(props) {
        super(props);
        let params = queryString.parse(window.location.search);
        let data = new FormData();
        for (let par in params) {
            data.append(par, params[par]);
        }
        this.state = {
            mode: 'subscribe',
            rights: params.vk_viewer_group_role,
            userId: params.vk_user_id,
            clientType: params.vk_platform,
            subscribeTitles: [],
            urlData: data,
            loading: true,
        };
        this.selectScreen = this.selectScreen.bind(this);
        this.resizeVkIframe = this.resizeVkIframe.bind(this);
        this.searchUpdate = this.searchUpdate.bind(this);
        this.height = 0;

    }


    selectScreen(e) {
        this.setState({mode: e.currentTarget.dataset.mode});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.resizeVkIframe();
    }


    resizeVkIframe() {
        if (this.state.clientType === 'desktop_web') {
            let height = document.getElementById(this.state.mode + "_size").scrollHeight + 110;
            if (this.height !== height) {
                this.height = height;
                connect.send('VKWebAppResizeWindow', {"width": 730, "height": height});
            }

        }
    }

    async componentDidMount() {
        let resp = await fetch(sprintf(reqSubscribeList, this.state.userId), {method: 'POST', body: this.state.urlData})
        let json = await resp.json();
        for (let el in json) {
            json[el].showed = true;
        }
        this.setState({subscribeTitles: json, loading: false});
        //this.resizeVkIframe();
    }

    async switchSubscribeStatus(titleId, titleType) {
        if (titleId !== 0 && titleId !== undefined && titleType !== undefined) {
            let resp = await fetch(sprintf(switchSubscribe, this.state.userId, titleId, titleType), {
                method: 'POST',
                body: this.state.urlData
            });
            let json = await resp.json();
            return json;
        } else {
            console.log("switch parameters are wrong")
        }
    }

    async loadStatistic(minimumId) {
        if (minimumId == undefined) {
            minimumId = 0;
        }
        let resp = await fetch(sprintf(reqStatisticUrl, minimumId), {method: 'POST', body: this.state.urlData});
        let json = await resp.json();
        return json;
    }

    searchUpdate(e) {
        for (let el in this.state.subscribeTitles) {
            if (this.state.subscribeTitles[el].anime_name.toLowerCase().indexOf(e.toLowerCase()) !== -1 || this.state.subscribeTitles[el].anime_name_rus.toLowerCase().indexOf(e.toLowerCase()) !== -1) {
                this.state.subscribeTitles[el].showed = true;
            } else {
                this.state.subscribeTitles[el].showed = false;
            }
            this.setState({subscribeTitles: this.state.subscribeTitles});
        }
    }


    render() {
        return (

            <Root activeView="subTitle">
                <View id="subTitle" activePanel={this.state.mode}>

                    <Panel id="subscribe">

                            <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen}
                                    client={this.state.clientType}/>
                            {this.state.clientType==='desktop_web'?<Search onChange={this.searchUpdate}/>:<FixedLayout vertical="top"><Search onChange={this.searchUpdate}/></FixedLayout>}
                        <div id="subscribe_size" style={{"paddingTop":(this.state.clientType==='desktop_web')?"0px":"60px"}}>
                            {this.state.loading && <PanelSpinner size="large"/>}


                            {this.state.subscribeTitles.map(item => (
                                (item.showed) ?
                                    <SubscribeBlock anime={item} switch={this.switchSubscribeStatus.bind(this)}
                                                    key={"sub_" + item.anime_id} resize={this.resizeVkIframe}/> :
                                    null

                            ))}
                        </div>

                    </Panel>
                    <Panel id="statistic_full">
                        <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen}/>
                        <StatisticBlock minimumId="0" loader={this.loadStatistic.bind(this)}
                                        client={this.state.clientType} mode={this.state.mode}
                                        resize={this.resizeVkIframe}/>
                    </Panel>
                    <Panel id="statistic_cur">
                        <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen}/>
                        <StatisticBlock minimumId="835" loader={this.loadStatistic.bind(this)}
                                        client={this.state.clientType} mode={this.state.mode}
                                        resize={this.resizeVkIframe}/>
                    </Panel>

                </View>
            </Root>


        );
    }
}

export default App;
